.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.main-content {
  position: absolute;
  width: 100%;
  height: 100%;
  min-height: 650px;
  top: 0;
  will-change: top;
  transition: .1s;
}

.container {
  margin: 0 auto;
  max-width: 940px;
  width: 95%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section {
  width: 100%;
  height: 100vh;
  min-height: 650px;
}

.title {
  font-size: 4.5rem;
  font-family: "gagalin";
  text-transform: uppercase;
  font-weight: 700;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', Arial, Helvetica, sans-serif;
  line-height: 1.42;
  color: #2f3234;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body,
html {
  height: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
}

a {
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: 'lifehack';
  src: url("../fonts/lifehack_medium-webfont.woff2") format("woff2"), url("../fonts/lifehack_medium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'ubuntu';
  src: url("../fonts/ubuntu-b-webfont.woff2") format("woff2"), url("../fonts/ubuntu-b-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'ubuntu';
  src: url("../fonts/ubuntu-light-webfont.woff2") format("woff2"), url("../fonts/ubuntu-light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'ubuntu';
  src: url("../fonts/ubuntu-medium-webfont.woff2") format("woff2"), url("../fonts/ubuntu-medium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'ubuntu';
  src: url("../fonts/ubuntu-regular-webfont.woff2") format("woff2"), url("../fonts/ubuntu-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'gagalin';
  src: url("../fonts/gagalin-regular-webfont.woff2") format("woff2"), url("../fonts/gagalin-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

.pagination {
  position: fixed;
  top: 15%;
  right: 5%;
  z-index: 50;
}

.pagination__item {
  margin-bottom: 15px;
}

.pagination__item:last-child {
  margin-bottom: 0;
}

.pagination__link {
  display: block;
  position: relative;
}

.pagination__link:before {
  display: block;
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background-color: #fff;
}

.pagination__item--active .pagination__link:after {
  content: '';
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.best {
  background: url(../img/best_section/bg.jpg) 0 0 no-repeat;
  background-size: cover;
}

.title--best {
  text-align: center;
  margin-bottom: 1.875rem;
}

.best__list {
  display: flex;
  justify-content: space-between;
}

.best__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  width: calc(100%/3);
}

.best__item div {
  text-align: center;
}

.best__item svg {
  width: 6.0625rem;
  height: 5.5625rem;
  margin-bottom: 1.0625rem;
}

.best__item h3 {
  font-family: "lifehack";
  font-size: 1.5625rem;
  line-height: 1.27;
  margin-bottom: 0.8125rem;
}

.best__item p {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 300;
  line-height: 1.7;
}

.burgers {
  background: linear-gradient(to bottom, #f8b43a 20%, #eb8c38 50%, #f8b43a 80%);
}

.container--burgers {
  flex-direction: row;
  align-items: center;
  max-width: 65rem;
  margin: 0 auto;
}

.left-block {
  padding-left: 10px;
}

.arrow-right__pic {
  fill: white;
  transform-origin: center;
  transform: rotate(-90deg);
}

.arrow-left__pic {
  fill: white;
  transform-origin: center;
  transform: rotate(90deg);
}

.slider__container {
  overflow: hidden;
  height: 90%;
}

.slider {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.slider__list {
  height: 100%;
  width: 100%;
  display: flex;
  position: relative;
  transition: right .3s;
}

.slider__item {
  height: 100%;
  width: 100%;
  display: flex;
  position: relative;
  align-items: center;
  flex-basis: 100%;
}

.ingredients {
  position: absolute;
  top: 10%;
  left: 0%;
  z-index: 100;
}

.ingredients__pic-wrap {
  width: 5.625rem;
  height: 5.625rem;
  background-color: #f08c33;
  border-radius: 5px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.ingredients__pic {
  fill: white;
}

.ingredients__desc {
  font-family: 'gagalin';
  font-size: 0.875rem;
  color: white;
}

.ingredients__dropdown {
  width: 16.875rem;
  height: 20.8125rem;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  padding-top: 15%;
  padding-left: 15%;
  padding-right: 25%;
  opacity: 0;
  transition: .3s opacity;
  position: absolute;
  top: 0;
  left: 5.625rem;
  z-index: -100;
}

.close-dropdown:before,
.close-dropdown:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 29px;
  width: 4px;
  display: block;
  background-color: #e35028;
}

.close-dropdown {
  display: none;
  position: absolute;
  top: 3.5%;
  right: 12%;
  cursor: pointer;
}

.close-dropdown:before {
  transform: rotate(45deg);
}

.close-dropdown:after {
  transform: rotate(-45deg);
}

.ingredients__list {
  width: 95%;
}

.ingredients__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ingredients__item-pic-wrap {
  width: 4.5rem;
  height: 3.125rem;
}

.ingredients__comp {
  font-size: 0.875rem;
  font-weight: 500;
}

.ingredients__weight {
  font-size: 0.875rem;
  font-weight: 300;
  margin-left: auto;
}

.kkal {
  width: 3.75rem;
  height: 4.875rem;
  position: absolute;
  bottom: 8%;
  left: 0%;
  opacity: 0.5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.kkal__pic {
  fill: white;
}

.kkal__pic-text {
  fill: white;
  font-size: 1.25rem;
  font-family: 'gagalin';
}

.kkal__desc {
  font-family: 'gagalin';
  font-size: 0.875rem;
  color: white;
}

.slider__pic-wrap {
  width: 50%;
}

.slider__pic {
  max-width: 30rem;
  width: 100%;
}

.slider__desc {
  width: 50%;
}

.slider__title {
  color: white;
  line-height: .9;
  margin-bottom: 10%;
  word-wrap: break-word;
}

.slider__text {
  color: white;
  font-size: 0.875rem;
  line-height: 1.7;
  margin-bottom: 5%;
}

.slider__order {
  display: flex;
  align-items: baseline;
}

.price {
  font-family: 'gagalin';
  font-size: 1.875rem;
  color: #e25028;
  margin-right: 3.5%;
  line-height: 1;
}

.price__number {
  font-size: 4.5rem;
}

.feedback__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  font-size: 0;
}

.feedback__item {
  display: flex;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
  width: 25%;
  height: 50%;
  position: relative;
  filter: grayscale(100%);
}

.feedback__item:after {
  content: '';
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 9;
  transition: 0.3;
}

.feedback__item:hover .feedback__container {
  opacity: 1;
}

.feedback__item:hover:after {
  opacity: 0.6;
}

.feedback__container {
  position: absolute;
  top: 14%;
  left: 7%;
  opacity: 0;
  z-index: 10;
  height: 100%;
}

.feedback__content {
  width: 100%;
  max-height: 65%;
  overflow: hidden;
  margin-bottom: 0.6875rem;
  padding-right: 20%;
}

.feedback__subtitle {
  font-size: 0.875rem;
  font-family: "gagalin";
  margin-bottom: 1.25rem;
  color: #fff;
  text-transform: uppercase;
}

.feedback__text {
  font-size: 0.875rem;
  color: #fff;
  line-height: 1.72;
  max-height: 10.2rem;
}

.feedback__btn {
  display: inline-block;
  padding: 0.625rem 0.9375rem;
  font-size: 0.8125rem;
  background-color: #2f3234;
  border-radius: 5px;
  text-transform: uppercase;
  text-align: center;
  color: white;
  font-weight: 500;
}

.feedback__btn:hover {
  background-color: black;
}

.feedback__btn--mobile {
  display: none;
}

.footer {
  height: 3.75rem;
  background: url(../img/map_section/footer-bg.jpg);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  color: #fff;
}

.footer__content {
  display: flex;
  flex-direction: row;
}

.footer__col {
  width: 50%;
  display: flex;
}

.footer__col--right {
  justify-content: flex-end;
  align-items: center;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contacts__address {
  font-size: 0.875rem;
  font-weight: 300;
  display: flex;
}

.contacts__phone {
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 700;
  color: #e25028;
}

.contacts__phone-icon {
  fill: #e25028;
  width: 13px;
  height: 11px;
  margin-right: 0.625rem;
}

.socials__list {
  display: flex;
}

.socials__item {
  margin-right: 1.25rem;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__icon {
  width: 20px;
  height: 20px;
  fill: #5c5c5c;
}

.socials__icon:hover {
  fill: #fff;
}

.hamburger-menu {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(47, 50, 52, 0.99);
  display: none;
}

.logo__link {
  width: 100%;
}

.logo--hamburger-menu {
  position: absolute;
  left: 2.5%;
  top: 0.5625rem;
}

.nav__list--hamburger-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 0;
  padding-left: 1.5625rem;
  height: 100vh;
}

.nav__list--hamburger-menu .nav__item {
  display: flex;
  flex-direction: column;
}

.nav__list--hamburger-menu .nav__item:after {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  margin-left: 0;
  margin-right: 0;
  text-align: center;
}

.nav__list--hamburger-menu .nav__link {
  font-size: 1.5rem;
}

.close:before,
.close:after {
  position: absolute;
  left: 15px;
  content: '';
  height: 29px;
  width: 4px;
  display: block;
  background-color: #fff;
}

.close {
  position: absolute;
  top: 2.5%;
  right: 8%;
  cursor: pointer;
}

.close:before {
  transform: rotate(45deg);
}

.close:after {
  transform: rotate(-45deg);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.5625rem;
}

.header__navigation {
  display: flex;
}

.logo {
  width: 5.625rem;
}

.logo__img {
  width: 100%;
}

.nav__list {
  font-size: 0;
  display: flex;
  align-items: center;
  margin-right: 0.9375rem;
}

.nav__item {
  display: flex;
  align-items: center;
}

.nav__item:after {
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 2px;
  background-color: #fff;
  display: block;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
}

.nav__item:last-child:after {
  display: none;
}

.nav__link {
  font-family: 'ubuntu';
  font-weight: 500;
  font-size: 0.8125rem;
  color: #fff;
  letter-spacing: 0.06px;
  text-transform: uppercase;
}

.order-btn {
  display: inline-block;
  border-radius: 5px;
  padding: 0.5625rem 1.0625rem;
  font-size: 0.8125rem;
  color: #fff;
  text-transform: uppercase;
  background-color: #e25028;
}

.order-btn:hover {
  background-color: #912e13;
}

.hamburger-menu-link {
  vertical-align: middle;
  position: relative;
  height: 1.375rem;
  align-items: center;
  margin-left: 1.875rem;
  display: none;
}

.hamburger-menu-link__bars,
.hamburger-menu-link__bars:before,
.hamburger-menu-link__bars:after {
  background: #fff;
  width: 1.5rem;
  height: 0.3125rem;
  border-radius: 0.1875rem;
}

.hamburger-menu-link__bars:before,
.hamburger-menu-link__bars:after {
  content: "";
  display: block;
  position: absolute;
}

.hamburger-menu-link__bars:before {
  top: 0;
}

.hamburger-menu-link__bars:after {
  bottom: 0;
}

.hello-block {
  background: url(../img/hello_section/video-bg.jpg) 0 0 no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
}

.hello-block__inner-container {
  display: flex;
  justify-content: flex-start;
}

.hello-block__container {
  display: flex;
  flex: 0.8;
}

.hello-block__content {
  display: flex;
  align-items: center;
  padding-left: 3%;
}

.hello-block__pic-wrap {
  width: 50%;
}

.hello-block__picture {
  width: 100%;
  max-width: 480px;
}

.hello-block__slogan {
  width: 50%;
  padding-top: 1.875rem;
}

.title--slogan {
  color: #f8b43a;
}

.slogan__text {
  font-size: 4.0625rem;
  font-family: "lifehack";
  color: #fff;
  line-height: 0.98;
}

.forward-arrow {
  position: absolute;
  bottom: 5%;
  left: 49.5%;
  cursor: pointer;
}

.forward-arrow__pic {
  width: 29px;
  height: 14px;
  fill: white;
}

.map {
  position: relative;
}

.menu {
  background: url(../img/menu_section/menu-bg.jpg) center center/cover no-repeat;
  position: relative;
}

.title--menu {
  position: absolute;
  top: 13%;
  left: 9%;
  color: white;
}

.h-accordeon__list {
  position: absolute;
  top: 0%;
  right: 0%;
  bottom: 0%;
  display: flex;
}

.h-accordeon__item {
  display: flex;
  height: 100%;
  right: 0;
  transition: .2s right;
  background-color: rgba(47, 50, 52, 0.93);
}

.h-accordeon__content {
  width: 0;
  overflow: hidden;
  height: 100%;
  transition: .6s all;
}

.h-accordeon__content-text {
  color: #fff;
  font-size: 0.875rem;
  font-weight: 300;
  padding-top: 3.75rem;
  padding-left: 2rem;
  padding-right: 0.3125rem;
  line-height: 1.72;
  opacity: 0;
  transition: .2s opacity;
}

.close-menu:before,
.close-menu:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 29px;
  width: 4px;
  display: block;
  background-color: #fff;
}

.close-menu {
  display: none;
  position: absolute;
  top: 3.5%;
  right: 12%;
  cursor: pointer;
}

.close-menu:before {
  transform: rotate(45deg);
}

.close-menu:after {
  transform: rotate(-45deg);
}

.h-accordeon__item--active .h-accordeon__content {
  width: 50vw;
}

.h-accordeon__item--active .h-accordeon__content-text {
  animation: text-appear .8s linear forwards;
}

@keyframes text-appear {
  75% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.h-accordeon__trigger {
  display: block;
  height: 100%;
  width: 5rem;
  position: relative;
}

.h-accordeon__trigger--meat {
  background: url(../img/menu_section/accord_meat.jpg) center center/cover no-repeat;
}

.h-accordeon__trigger--vegetarian {
  background: url(../img/menu_section/accord_vegetarian.jpg) 90% center/cover no-repeat;
}

.h-accordeon__trigger--diet {
  background: url(../img/menu_section/accord_diet.jpg) 90% center/cover no-repeat;
}

.h-accordeon__trigger:after {
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(71, 74, 75, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
}

.h-accordeon__title {
  font-family: 'gagalin';
  color: white;
  font-size: 2.5rem;
  text-align: center;
  white-space: nowrap;
  position: absolute;
  transform: rotate(-90deg) translateY(-50%);
  transform-origin: 0 0;
  left: 50%;
  top: 100%;
  margin-top: -2.1875rem;
  z-index: 10;
}

.order {
  background: url(../img/order_section/order-bg.jpg) 0 0/cover no-repeat;
  color: #fff;
}

.order__container {
  align-items: center;
}

.title--order {
  color: #f8b43a;
  margin-bottom: 1.25rem;
}

.form__content {
  display: flex;
}

.form__col {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-right: 1.25rem;
}

.form__col:last-child {
  margin-right: 0;
}

.form__row {
  display: flex;
  margin-bottom: 0.9375rem;
}

.form__title {
  font-size: 0.875rem;
  font-weight: 300;
  margin-bottom: 0.3125rem;
}

.form__block {
  flex: 1;
  margin-right: 1.25rem;
}

.form__block:last-child {
  margin-right: 0;
}

.form__input {
  border: none;
  border-radius: 5px;
  padding: 0.625rem 0.9375rem;
  width: 100%;
}

.form__input--textarea {
  height: 110px;
  resize: none;
}

.form__radio-lbl {
  display: flex;
  align-content: center;
  margin-right: 0.9375rem;
}

.form__custom-radio {
  width: 16px;
  height: 16px;
  border-radius: 8px;
  background-color: #fff;
  position: relative;
  margin-right: 0.5rem;
}

.form__radio {
  position: absolute;
  left: -999999px;
}

.form__radio:checked ~ .form__custom-radio:after {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background-color: #e25028;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form__custom-check {
  width: 16px;
  height: 16px;
  border-radius: 2px;
  background-color: #fff;
  position: relative;
  margin-right: 0.625rem;
}

.form__check {
  position: absolute;
  left: -99999px;
}

.form__check:checked + .form__custom-check::after {
  content: '';
  display: block;
  height: 7px;
  width: 10px;
  border-left: 4px solid #e25028;
  border-bottom: 4px solid #e25028;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%) rotate(-45deg);
}

.button {
  border: 0;
  cursor: pointer;
  margin-right: 0.625rem;
}

.reset-btn {
  background: transparent;
  color: white;
  font-size: 0.875rem;
  font-weight: 300;
}

.overlay {
  display: flex;
  background-color: rgba(47, 50, 52, 0.93);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  justify-content: center;
}

.overlay__container {
  width: 460px;
  min-height: 255px;
  display: block;
  background-color: #fff;
  border-radius: 5px;
  padding-left: 15px;
  padding-top: 28px;
  padding-right: 23px;
  padding-bottom: 15px;
  position: relative;
}

.overlay__name {
  font-family: 'gagalin';
  font-size: 14px;
  margin-bottom: 25px;
}

.overlay__text {
  font-weight: 300;
  font-size: 14px;
}

.overlay__close:before,
.overlay__close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 29px;
  width: 4px;
  display: block;
  background-color: #ec8468;
}

.overlay__close {
  position: absolute;
  top: 7%;
  right: 10%;
  cursor: pointer;
}

.overlay__close:before {
  transform: rotate(45deg);
}

.overlay__close:after {
  transform: rotate(-45deg);
}

