.v-accordeon__content {
  display: flex;
  overflow: hidden;
  justify-content: space-around;
  align-items: center;
  height: 0;
  opacity: 0;
  transition: height .5s;
}

.v-accordeon__item--active .v-accordeon__name {
  color: #f8b43a;
}

.v-accordeon__item--active .v-accordeon__name:before {
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top-color: #f8b43a;
  border-bottom-width: 0;
  margin-right: 0.3125rem;
  transform: rotate(180deg);
}

.v-accordeon__item--active .v-accordeon__content {
  height: 9.0625rem;
  animation: acco-content .3s linear forwards;
}

@keyframes acco-content {
  75% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

